@import "../../mixins/mixins";

@mixin reusable-address-box() {
  border-width: 0;
  border-radius: 5px;
  // width: 100%;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.2);
}

.mapContainer {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 10%;
  border-radius: 10px;
  box-shadow: 0 0 5px #ccc;
  max-height: calc(100vh - 350px);
  min-height: 450px;
  height: 100%;
  max-width: calc(100vw - 1200px);
  min-width: 730px;
  width: 100%;
  font-family: "Inter", sans-serif;
  font-size: small;
  background-color: #fcfcfc;
  justify-content: center;
  align-items: center;
  padding: 25px;
  z-index: 10000;
  overflow-y: auto;

  h3 {
    align-self: flex-start;
    margin-top: -15px;
  }

  .addressContainer {
    display: flex;
    flex-direction: row;
    margin-bottom: 15px;
    min-height: 30px;
    align-self: flex-start;
    width: 100%;

    .inputContainer,
    .houseInputContainer {
      position: relative;
      display: flex;
      width: 70%;
      max-width: 600px;
    }
    .houseInputContainer {
      margin-left: 10px;
      max-width: 20%;
    }

    .input,
    .inputHouse {
      padding: 0.5em;
      @include reusable-address-box;
      box-sizing: border-box;
      padding-left: 20px;
      font-family: "Inter", sans-serif;
      text-overflow: ellipsis;
      width: 100%;

      @include focus-style(#ccc);
    }

    .buttonStyle {
      @include reusable-address-box;
      width: 20%;
      min-width: 89px;
      background-color: #fcfcfc;
      margin-left: 10px;
      cursor: pointer;

      @include interactive-bg(#fcfcfc);
    }
    .buttonText {
      color: rgb(31, 31, 31);
      font-family: "Inter", sans-serif;
      text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.2);
      font-size: 12px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
    }
  }

  .map {
    width: 730px;
    height: 100%;
  }

  .icon {
    align-self: flex-end;
    cursor: pointer;
  }
}

@media (max-width: 1024px) {
  .mapContainer {
    position: relative;
    box-sizing: border-box;
    top: 0;
    border-radius: 0;
    box-shadow: 0;
    max-height: 100%;
    min-height: 100%;
    max-width: 100%;
    min-width: 100%;
    .map {
      width: 100%;
      height: 100%;
    }
  }
}

@media (max-width: 512px) {
  .mapContainer {
    h3 {
      margin-top: 0;
    }
    .addressContainer {
      .input {
        padding-left: 10px;
      }
      .buttonText {
        font-size: 10px;
      }
      .buttonStyle {
        min-width: 76px;
      }
    }
  }
}
